<template>
    <main class="content container">
        <div class="content__top">
          <ul class="breadcrumbs">
            <li class="breadcrumbs__item">
              <router-link class="breadcrumbs__link" :to="{name: 'main'}">
                Каталог
              </router-link>
            </li>
            <li class="breadcrumbs__item">
              <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
                Корзина
              </router-link>
            </li>
            <li class="breadcrumbs__item">
              <a class="breadcrumbs__link">
                Оформление заказа
              </a>
            </li>
          </ul>
    
          <h1 class="content__title">
            {{ orderInfo.status.title }} <span>№ {{orderInfo.id}}</span>
          </h1>
        </div>
    
        <section class="cart">
          <form class="cart__form form" action="#" method="POST">
            <div class="cart__field">
              <p class="cart__message">
                Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа. 
                Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
              </p>
    
              <ul class="dictionary">
                <BaseFormOrderInfo title="Получатель" :info="orderInfo.name"/>
                <BaseFormOrderInfo title="Адрес доставки" :info="orderInfo.address"/>
                <BaseFormOrderInfo title="Телефон" :info="orderInfo.phone"/>
                <BaseFormOrderInfo title="Email" :info="orderInfo.email"/>
                <BaseFormOrderInfo title="Способ оплаты" info="картой при получении"/>  
               
              </ul>
            </div>
    
            <div class="cart__block">
              <ul class="cart__orders">
                <OrderInfoItem v-for="item in orderInfo.basket.items" :key="item.id" :item='item'/>
              </ul>
              
              <div class="cart__total">
                <p>Доставка: <b>500 ₽</b></p>
                <p>Итого: <b> {{  Object.keys(orderInfo.basket.items).length }} </b> товара на сумму <b>{{ orderInfo.totalPrice }} ₽</b></p>
              </div>
            </div>
          </form>
        </section>
      </main>
</template>

<script>
    import numberFormat from "@/helpers/numberFormat";
    import OrderInfoItem from "@/components/OrderInfoItem.vue";
    import BaseFormOrderInfo from "@/components/BaseFormOrderInfo.vue";
   
    export default {
        components: {OrderInfoItem, BaseFormOrderInfo},
        filters: {numberFormat},
        created(){
            if(this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id) {
                return
            }
            this.$store.dispatch('loadOrderInfo', this.$route.params.id)
        },
        computed: {
            orderInfo() {
                return this.$store.state.orderInfo
            }
        }
        
    }
</script>